// Dependencies
import React, { useState } from "react";
import { BsBookmark, BsBookmarkFill } from "react-icons/bs";
import { motion } from "framer-motion";

// Styles
import "./tailwind.output.css";

const items = [0, 1, 2, 3, 4, 5];

const Button = ({ action }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [bookmarked, setBookmarked] = useState(false);

  const variants = {
    loading: {
      opacity: [1, 0, 1],
      transition: { repeat: Infinity }
    },
    stop: {
      opacity: 1
    }
  };

  const onClick = async () => {
    setIsLoading(true);
    await action();
    setBookmarked((curr) => !curr);
    setIsLoading(false);
  };

  return (
    <motion.button
      className="flex focus:outline-none"
      onClick={onClick}
      disabled={isLoading}
    >
      <motion.span variants={variants} animate={isLoading ? "loading" : "stop"}>
        {bookmarked ? <BsBookmarkFill /> : <BsBookmark />}
      </motion.span>
    </motion.button>
  );
};

const App = () => {
  const toggleAction = (item) => () =>
    new Promise((resolve) => {
      setTimeout(() => {
        resolve(item);
      }, 3000);
    });

  return (
    <div className="min-h-screen">
      <h2 className="text-center text-4xl font-bold text-blue-900">List:</h2>

      <ul className="w-full m-auto bg-gray-200 p-20">
        {items.map((item) => (
          <li key={item} className="flex items-center space-x-2">
            <span>Item {item}</span>
            <Button action={toggleAction(item)} />
          </li>
        ))}
      </ul>
    </div>
  );
};

export default App;
